<template>
  <div
    class="b-icon"
    :class="[
      sizeProp.length > 0 ? 'b-icon--size-' + sizeProp : '',
      styleProp.length > 0 ? 'b-icon--style-' + styleProp : '',
      colorProp.length > 0 ? 'b-icon--color-' + colorProp : '',
      classProp.length > 0 ? classProp : ''
    ]"
  >
    <span v-if="accessibilityTextProp.length > 0" class="b-hide">{{ accessibilityTextProp }}</span>

    <slot />
  </div>
</template>

<script>
export default {
  props: {
    classProp: {
      type: String,
      default() {
        return ''
      }
    },
    sizeProp: {
      type: String,
      default() {
        return ''
      }
    },
    styleProp: {
      type: String,
      default() {
        return ''
      }
    },
    colorProp: {
      type: String,
      default() {
        return ''
      }
    },
    accessibilityTextProp: {
      type: String,
      default() {
        return ''
      }
    }
  }
}
</script>